@charset "UTF-8";

@media screen and (min-width: 1025px) {
  .HeaderWrap{
    display: flex;
    width: 1170px;
    height: 80px;
    margin: 0 auto;
    justify-content: space-between;
    position: relative;
  }
  .ToggleSwitch{
    position: relative;
    width: 48px;
    height: 48px;
    margin: 16px 0;
    background: var(--main-color01);
    cursor: pointer;
    user-select: none;
  }
  .ToggleSwitch span{
    display: inline-block;
    position: absolute;
    width: 32px;
    height: 2px;
    margin: 0 auto;
    background: var(--main-color02);
    transition: all 0.3s;
    left : 8px;
  }
  .ToggleSwitch span:first-child{
    top: 18px;
  }
  .ToggleSwitch span:last-child{
    bottom: 18px;
  }
  .ToggleSwitch.Active span:first-child{
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
  }
  .ToggleSwitch.Active span:last-child{
    bottom: 50%;
    transform: translateY(50%) rotate(-45deg);
  }
  .HeaderBtn-wrap{
    display: flex;
    align-items: center;
  }
}

@media screen and (min-width:768px) and (max-width:1024px) {
  .HeaderWrap{
    display: flex;
    width: 100%;
    height: 80px;
    margin: 0 auto;
    justify-content: space-between;
    position: relative;
    box-sizing: border-box;
    padding: 0 16px;
  }
  .HeaderBtn-wrap > .GNB{display: none;}
  .ToggleSwitch{
    position: relative;
    width: 48px;
    height: 48px;
    margin: 16px 0;
    background: var(--main-color01);
    cursor: pointer;
    user-select: none;
