  .ToggleSwitch span{
    display: inline-block;
    position: absolute;
    width: 32px;
    height: 2px;
    margin: 0 auto;
    background: var(--main-color02);
    transition: all 0.3s;
    left : 8px;
  }
  .ToggleSwitch span:first-child{
    top: 18px;
  }
  .ToggleSwitch span:last-child{
    bottom: 18px;
  }
  .ToggleSwitch.Active span:first-child{
    top: 50%;
    transform: translateY(-50%) rotate(45deg);
  }
  .ToggleSwitch.Active span:last-child{
    bottom: 50%;
    transform: translateY(50%) rotate(-45deg);
  }
  .HeaderBtn-wrap{
    display: flex;
    align-items: center;
  }
}