@charset "UTF-8";

.main-event{
  width: 1170px;
  margin: 100px auto;
}

.main_event-list{
  display: grid;
  width: 100%;
  grid-template-columns: repeat(6, auto);
  grid-gap: 30px;
}
.main_event-sales{
  width: calc(100%);
  height: 100%;
  grid-column: 1/3;
  grid-row: 1/3;
  background: var(--main-color01);
  display: flex;
  align-items: center;
}

.muji_sale-item{
  width: 100%;
  height: 100%;
}
.muji_sale-itemimg{
  width: 170px;
  height: 170px;
}