@charset "UTF-8";

.main-title{
  font-weight: 800;
  font-size: 32px;
  text-align: center;
  margin-bottom:40px;
}
.main-info span{
  display: inline;
  border-bottom: 3px solid var(--main-color01);
}
.main-info{
  font-weight: 500;
  font-size: 16px;
  line-height: 26px;
  text-align: center;
  margin-bottom: 12px;
}

.main-item-title{
  font-weight: 600;
  font-size: 16px;
  text-align: left;
  margin-bottom: 15px;
}
.main-item-price{
  color: var(--main-color01);
  font-weight: 300;
  font-size: 16px;
  text-align: left;
}
.swiper-pagination-bullet{
  border-radius: 0px;
  margin: 0px 7px !important;
}
.swiper-pagination-bullet-active{
  background: var(--main-color01);
}
.main-banner{
  margin-bottom: 100px;
}
.main-banner-slide{
  width: 100%;
  height: 620px;
}
.main-banner-slide img{
  width: 100%;
  height: 580px;
  object-fit: cover;
}