@charset "UTF-8";

.muji_week-new{
  width: 1170px;
  margin: 0 auto;
  text-align: center;
  position: relative;
}

/* 슬라이드 서식 */
.muji_new-slide{
  width: 100%;
  height: 260px;
  position: relative;
  overflow: hidden;
}
.muji_new-slide-item{
  display: block;
  width: 170px;
  height: 260px;
}
.muji_new-slide-item img{
  width: 100%;
  height: 170px;
  object-fit: cover;
}
.muji_new-slide-item .slide-info{
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  opacity: 0;
  transition: all 0.3s;
}

.swiper-button-prev, .swiper-button-next{
  width: 50px;
  height: 50px;
  line-height: 50px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}
.swiper-button-prev{
  left: -50px
}
.swiper-button-next{
  right: -50px;
}
