@charset "UTF-8";

.main-walker{
  width: 1170px;
  margin: 100px auto;
}

.main_walker-list{
  display: grid;
  width: 100%;
  grid-template-columns: repeat(6, auto);
  grid-gap: 30px;
}
.main_walker-banner{
  width: 570px;
  height: 570px;
}
.muji_walker-item{
  width: 100%;
  height: 100%;
}
.muji_walker-itemimg{
  width: 268px;
  height: 480px;
  object-fit: cover;
}